$color: teal;
$dot-size: 20px;
$dot-margin: 5px;
$first: $dot-size + $dot-margin;
$second: (2 * $dot-size) + (2 * $dot-margin);
$third: (3 * $dot-size) + (3 * $dot-margin);
$fourth: (4 * $dot-size) + (4 * $dot-margin);
$fifth: (5 * $dot-size) + (5 * $dot-margin);
$hide: -1 * $dot-size;

.snake {
  &:after {
    animation: snake 2s infinite both ease;
    border-radius: 100%;
    content: "";
    height: $dot-size;
    left: 50%;
    margin-left: -(($third / 2) + $dot-size);
    margin-top: -($dot-size / 2);
    position: absolute;
    top: 50%;
    width: $dot-size;
  }
}

@keyframes snake {
  0%,
  5% {
    box-shadow: $first $first 0 $hide $color, $second $first 0 $hide $color,
      $third $first 0 $hide $color, $third 0 0 $hide $color,
      $third (-$first) 0 $hide $color, $second (-$first) 0 $hide $color,
      $first (-$first) 0 $hide $color, $first 0 0 $hide $color,
      $second 0 0 $hide $color;
  }
  10% {
    box-shadow: $first $first 0 0 $color, $second $first 0 $hide $color,
      $third $first 0 $hide $color, $third 0 0 $hide $color,
      $third (-$first) 0 $hide $color, $second (-$first) 0 $hide $color,
      $first (-$first) 0 $hide $color, $first 0 0 $hide $color,
      $second 0 0 $hide $color;
  }
